'use client';

import { useEffect, useState } from 'react';
import { doc, getDoc } from 'firebase/firestore';
import { auth, db } from '@/src/firebaseConfig';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import { useRouter } from 'next/navigation';
import NotificationDialog from '@/src/components/ui/NotificationDialog';

export default function CompanyDashboardPage() {
  const [company, setCompany] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  const [dialog, setDialog] = useState({
    open: false,
    type: 'info' as 'success' | 'error' | 'info',
    title: '',
    description: '',
  });

  const showDialog = (type: 'success' | 'error' | 'info', title: string, description = '') => {
    setDialog({ open: true, type, title, description });
  };

  useEffect(() => {
    const fetchCompany = async () => {
      try {
        const user = auth.currentUser;
        if (!user) return;

        const userDoc = await getDoc(doc(db, 'users', user.uid));
        const userData = userDoc.data();

        if (!userData?.companyId) {
          showDialog('error', 'No company linked', 'You are not linked to any rental company.');
          router.push('/my-business');
          return;
        }

        const companyDoc = await getDoc(doc(db, 'rentalCompanies', userData.companyId));

        if (!companyDoc.exists()) {
          showDialog('error', 'Company not found', 'The rental company data could not be found.');
          return;
        }

        setCompany({ id: companyDoc.id, ...(companyDoc.data() || {}) });
        setLoading(false);
      } catch (error) {
        console.error('‚ùå Error fetching company:', error);
        showDialog('error', 'Error', 'Failed to load company data.');
      }
    };

    fetchCompany();
  }, [router]);

  return (
    <div
      className="min-h-screen flex flex-col bg-cover bg-center"
      style={{ backgroundImage: "url('/assets/images/Cover2.jpg')" }}
    >
      <Header />

      <main className="flex-1 px-4 py-24 flex justify-center">
        <div className="w-full max-w-5xl bg-white bg-opacity-90 rounded-3xl shadow-2xl p-10 border border-gray-200 space-y-8">
          {loading ? (
            <p className="text-center text-gray-600">Loading company info...</p>
          ) : company ? (
            <>
              <div className="text-center">
                <h1 className="text-2xl font-bold text-gray-800">{company.name}</h1>
                <div className="w-16 h-[3px] bg-[#00d289] mx-auto mt-2 mb-4 rounded-full" />
                <p className="text-sm text-gray-600">{company.description}</p>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <DashboardCard
                  title="Rental Stations"
                  desc="Create and manage station locations"
                  onClick={() => router.push('/my-business/stations')}
                />
                <DashboardCard
                  title="eBike Models"
                  desc="Define models used in your fleet"
                  onClick={() => router.push('/my-business/ebike-models')}
                />
                <DashboardCard
                  title="eBikes"
                  desc="Manage individual vehicles and availability"
                  onClick={() => router.push('/my-business/ebikes')}
                />
              </div>
            </>
          ) : (
            <p className="text-center text-red-600">
              ‚ùå Could not load company data.
            </p>
          )}
        </div>
      </main>

      <Footer />

      <NotificationDialog
        open={dialog.open}
        type={dialog.type}
        title={dialog.title}
        description={dialog.description}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}

function DashboardCard({
  title,
  desc,
  onClick,
}: {
  title: string;
  desc: string;
  onClick: () => void;
}) {
  return (
    <div
      onClick={onClick}
      className="cursor-pointer bg-white border border-gray-200 rounded-2xl p-6 text-center shadow-md hover:shadow-xl hover:-translate-y-1 transition-all group"
    >
      <h2 className="text-lg font-semibold text-gray-800 group-hover:text-[#00d289] mb-2">
        {title}
      </h2>
      <p className="text-sm text-gray-600">{desc}</p>
    </div>
  );
}
